body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading{
  height: 100vh;
    width: 100%;
    margin: 0;
    padding: 0;
    display: grid;
  position: relative;
}

.navigation{
  z-index: 2;
  position: absolute;
  text-align: center;
  background-color: white;
  width: auto;
  padding: 0.5rem;
  
}

.background{
  position: fixed;
  z-index: 3;
  background-color: rgb(0, 14, 31);
  height: 100vh;
  width: 100vw;
}

.PopUp{
  position: fixed;
  z-index: 4;
  justify-content: center;
  text-align: center;
  padding: 2rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  
 
}

.description{
  order: 1;
  font-size: 24px;
  color: white;
}

.loader{
  cursor: pointer;
  z-index: 5;
  color: rgb(0, 37, 80);
  justify-content: center;
  text-align: center;
  padding: 1rem;
  width: 12rem;
  border-radius: 1rem;
  margin: auto;
  order: 2;
}