.App{
    height: 100vh;
    width: 100%;
    margin: 0;
    padding: 0;
    display: grid;
  position: relative;
}

Canvas{
    z-index: 1;
    height: 100vh;
    width: 100%;
    position: relative;
    cursor:pointer;
}

.navigation{
    z-index: 2;
    position: absolute;
    text-align: center;
    background-color: white;
    width: auto;
    padding: 0.5rem;
    
}

.background{
    position: fixed;
    z-index: 3;
    background-color: rgba(0, 14, 31, 1);
    height: 100vh;
    width: 100vw;
}

.exit{
    cursor: pointer;
    z-index: 5;
    color: rgb(0, 37, 80);
    justify-content: center;
    background-color: rgb(255, 255, 255);
    text-align: center;
    padding: 1rem;
    width: 12rem;
    border-radius: 1rem;
    margin: auto;
    order: 2;
}